.header {
  background: var(--core-color-white);
  position: fixed;
  bottom: 0;
  z-index: 500;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 var(--core-space-lg);
  height: var(--header-height);
}

.header .logo {
  display: none;
}

.header nav {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header nav > * {
  height: var(--core-size-xl);
  text-align: center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-left: var(--core-space-sm);
}

.header nav span {
  padding-top: var(--core-space-sm);
  display: block;
}

.header nav i {
  margin-right: 0;
}

@media (min-width: 800px) {
  .header {
    position: relative;
    top: 0;
  }

  .header .logo {
    display: block;
  }

  .header nav {
    width: auto;
  }

  .header nav span {
    display: inline-block;
  }

  .header nav > * {
    height: var(--core-size-md);
    flex-direction: row;
  }

  .header nav i {
    margin-right: var(--core-space-md);
  }

  .header nav span {
    padding-top: 0;
  }
}
